.contacts {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 27px;

    color: #9fadc2;
  }

  &__info-link {
    white-space: nowrap;
    color: inherit;
    text-decoration: none;

    &:hover {
      color: $colorBlue;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__flex-wrapper {
    @media (max-width: 1023px) {
      display: flex;
      justify-content: space-around;
    }

    @media (max-width: 375px) {
      display: block;
    }
  }
}
